@use '../base/variables' as v;
@use '../base/mixins' as m;


.text_info{
    font-size: 1.5rem;
    margin: 0;
    text-align: center;
}

.sub_titulo{
    text-align: center;
    font-weight: v.$regular;
    margin: 1.5rem 0 1rem;
}

.titulo_cita{
color: v.$negro;
font-size: 3rem;
margin-bottom: 1rem;
text-align: center;
font-weight: v.$regular;

@include m.telefono{
    font-size: 3.9rem;
}

}

.descripcion{
    text-align: center;
    margin-bottom: 5rem;
}

.seccion{
    &:nth-child(2),
    &:nth-child(3){
        display: none;
    }
}

.tabs{
    border-radius: 1rem;
    border: 1px solid rgb(188, 188, 188);
    display: flex;
    justify-content: center;
    button{
        padding: 0rem;
        width: 100%;
        background-color: unset;
        border: none;
        cursor: pointer;
        border-radius: 1.2rem;
    }

}

.activo{
    background-color: #000;
    color: #fff;
}