/** Media Queries **/
.text_info {
  font-size: 1.5rem;
  margin: 0;
  text-align: center;
}

.sub_titulo {
  text-align: center;
  font-weight: 400;
  margin: 1.5rem 0 1rem;
}

.titulo_cita {
  color: #1a1b15;
  font-size: 3rem;
  margin-bottom: 1rem;
  text-align: center;
  font-weight: 400;
}
@media (min-width: 480px) {
  .titulo_cita {
    font-size: 3.9rem;
  }
}

.descripcion {
  text-align: center;
  margin-bottom: 5rem;
}

.seccion:nth-child(2), .seccion:nth-child(3) {
  display: none;
}

.tabs {
  border-radius: 1rem;
  border: 1px solid rgb(188, 188, 188);
  display: flex;
  justify-content: center;
}
.tabs button {
  padding: 0rem;
  width: 100%;
  background-color: unset;
  border: none;
  cursor: pointer;
  border-radius: 1.2rem;
}

.activo {
  background-color: #000;
  color: #fff;
}